.login-form {
  background-color: var(--color2);
  border-radius: 4px;
  box-shadow: 0 0 10px rgb(136, 136, 136);
  color: var(--color3);
  margin: 20vh auto;
  max-width: 400px;
  padding: 20px;
}

.login-form .label {
  font-size: 1rem;
  margin: 10px 0;
}

.login-form .btn {
  margin: 20px auto 10px;
  width: 100px;
}

